/*
Theme Name: useji-child
Version: 1.0
Description: A child theme of Clean by MyThemeShop
Template: mts_clean
Text Domain: clean
*/
/* ------------------------------------------------------
Site Utility
--------------------------------------------------------- */
@font-face {
	font-family: 'icomoon';
	src: url("fonts/icomoon.eot?531r0y");
	src: url("fonts/icomoon.eot?531r0y#iefix") format("embedded-opentype"), url("fonts/icomoon.ttf?531r0y") format("truetype"), url("fonts/icomoon.woff?531r0y") format("woff"), url("fonts/icomoon.svg?531r0y#icomoon") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-shopping-cart:before {
	content: "\e902";
}

.icon-heart-o:before {
	content: "\e900";
}

.icon-user-circle-o:before {
	content: "\e901";
}

/* ------------------------------------------------------
Site Header section
--------------------------------------------------------- */
.single-product #site-header {
	border-bottom: 0;
}

#site-header {
	border-bottom: 1px solid rgba(0, 0, 0, 0.12);
	float: none;
}

#site-header * {
	float: none;
}

#site-header .container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.875rem 0.9375rem 20px;
}

#site-header::before, #site-header::after {
	display: none;
}

#logo {
	font-size: 2.25rem;
	font-weight: 500;
	line-height: 1;
	text-transform: capitalize;
}

#logo a {
	color: #20303c;
}

#logo a:focus, #logo a:hover {
	color: #96588a;
}

/* ------------------------------------------------------
Pimary Site Navigation
--------------------------------------------------------- */
#primary-navigation .navigation .menu {
	display: flex;
}

#shop-navigation .shop-menu {
	display: flex;
}

#shop-navigation .shop-menu li {
	margin-right: 15px;
}

#shop-navigation .shop-menu li:last-child {
	margin-right: 0;
}

#shop-navigation a {
	color: #20303c;
}

#shop-navigation a:focus, #shop-navigation a:hover {
	color: #96588a;
}

.woo-icon {
	font-size: 0;
}

.woo-icon .fa {
	font-size: 1.25rem;
}

/* ------------------------------------------------------
Woocommerce Breadcrumbs
--------------------------------------------------------- */
.custom-breadcrumb {
	background-color: #f2f2f2;
	padding-top: 0.9375rem;
	padding-bottom: 0.9375rem;
}

.woocommerce .woocommerce-breadcrumb {
	font-size: 0.75em !important;
	margin-bottom: 0 !important;
}

/* ------------------------------------------------------
Woocommerce styles
--------------------------------------------------------- */
.woocommerce .quantity .qty {
	border-radius: 0 !important;
}

.woocommerce a.button,
.woocommerce-page a.button,
.woocommerce button.button,
.woocommerce-page button.button,
.woocommerce input.button,
.woocommerce-page input.button,
.woocommerce #respond input#submit,
.woocommerce-page #respond input#submit,
.woocommerce #content input.button,
.woocommerce-page #content input.button {
	border-radius: 0 !important;
}

.pagination a,
.pagination .page-numbers.dots,
.woocommerce nav.woocommerce-pagination ul li a {
	font-size: 1.25rem;
}

.woocommerce nav.woocommerce-pagination ul li span.current,
.woocommerce-page nav.woocommerce-pagination ul li span.current,
.woocommerce #content nav.woocommerce-pagination ul li span.current,
.woocommerce-page #content nav.woocommerce-pagination ul li span.current {
	font-size: 1.25rem !important;
}

.woocommerce-account .woocommerce-MyAccount-navigation {
	margin-bottom: 2.5rem;
}

.woocommerce-account .woocommerce-MyAccount-content {
	width: 100% !important;
}

@media (min-width: 600px) {
	.woocommerce-account .woocommerce-MyAccount-content {
		width: 70% !important;
	}
}

.post-type-archive-product article {
	width: 73%;
}

.post-type-archive-product .mts-sidebar-shop-sidebar {
	width: 20%;
}

.single-product #page .article {
	width: 100%;
}

.single-product .yith-wcwl-add-button,
.single-product .yith-wcwl-wishlistexistsbrowse,
.single-product .yith-wcwl-wishlistaddedbrowse {
	font-size: 1rem;
	border-top: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	margin-bottom: 20px;
	padding: 10px 0;
}

.single-product .yith-wcwl-add-button .feedback,
.single-product .yith-wcwl-wishlistexistsbrowse .feedback,
.single-product .yith-wcwl-wishlistaddedbrowse .feedback {
	display: block;
}

.show-title-form {
	background-color: transparent !important;
	color: #20303c;
}

/* ------------------------------------------------------
Homepage Page Template Styles
--------------------------------------------------------- */
.page-template-page-home #page {
	padding-top: 2.5rem;
}

.page-template-page-home article header {
	float: none;
}

.page-template-page-home article header .entry-title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

.home .page h1.title {
	font-size: 0;
	color: transparent;
	margin: 0;
}

/* ------------------------------------------------------
Homepage Grid
--------------------------------------------------------- */
.home-grid .wc-block-grid__product-link {
	display: block;
	position: relative;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-image {
	background-color: #96588a;
	margin-bottom: 0;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-image img {
	display: block;
	opacity: 1;
	transition: 0.5s;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-title {
	font-size: 1.375rem;
	font-weight: normal;
	color: white;
	margin-bottom: 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column;
	justify-content: center;
	opacity: 0;
	transition: 0.5s;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-title::before, .home-grid .wc-block-grid__product-link .wc-block-grid__product-title::after {
	content: '';
	position: absolute;
	top: 1.5rem;
	right: 1.5rem;
	bottom: 1.5rem;
	left: 1.5rem;
	transition: 0.5s;
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-title::before {
	border-top: 1px solid white;
	border-bottom: 1px solid white;
	transform: scale(0, 1);
}

.home-grid .wc-block-grid__product-link .wc-block-grid__product-title::after {
	border-right: 1px solid white;
	border-left: 1px solid white;
	transform: scale(1, 0);
}

.home-grid .wc-block-grid__product-link:focus img, .home-grid .wc-block-grid__product-link:hover img {
	opacity: 0.2;
}

.home-grid .wc-block-grid__product-link:focus .wc-block-grid__product-title, .home-grid .wc-block-grid__product-link:hover .wc-block-grid__product-title {
	opacity: 1;
}

.home-grid .wc-block-grid__product-link:focus .wc-block-grid__product-title::before, .home-grid .wc-block-grid__product-link:focus .wc-block-grid__product-title::after, .home-grid .wc-block-grid__product-link:hover .wc-block-grid__product-title::before, .home-grid .wc-block-grid__product-link:hover .wc-block-grid__product-title::after {
	transform: scale(1);
}

/* ------------------------------------------------------
Site Footer section
--------------------------------------------------------- */
#site-footer {
	background-color: #f0f1f2 !important;
	border-top: 0;
}

#move-to-top {
	display: none;
}
